.load {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.js-breed-select {
  width: 100%;
  color: #333;
  background-color: #f8f8f8;
  border: 2px solid #3498db;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s ease-in-out;
}

.js-breed-select:focus {
  border-color: #2980b9;
}

.js-breed-select:after {
  content: "▼";
  pointer-events: none;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.js-breed-select option {
  color: #333;
  background-color: #f8f8f8;
  border-bottom: 1px solid #ddd;
  padding: 10px;
  font-size: 16px;
}

.loader, .load, .error {
  color: #e74c3c;
  margin-top: 10px;
  font-size: 18px;
}

.cat-info {
  text-align: left;
  margin-top: 20px;
  display: none;
}

.js-breed-select option[value=""] {
  color: #999;
}

